@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Helvetica:ital,wght@0,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Spline+Sans:ital,wght@0,400;0,500;0,600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Epilogue:ital,wght@0,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:ital,wght@0,400;0,600&display=swap");

body {
  @apply leading-[normal] m-0;
}
@layer base {
  *,
  *::before,
  *::after {
    border-width: 0;
  }
}
